import React, { Component } from "react";
import axios from "./axios";

class DownloadsToday extends Component {
  state = {
    data: [],
  };

  componentDidMount() {
    var slug = localStorage.getItem("slug");

    const proxyurl = "https://cors-anywhere.herokuapp.com/";
    const url =
      "http://api.wordpress.org/stats/plugin/1.0/downloads.php?slug=" +
      slug +
      "&limit=10&historical_summary=1";

    axios.get(proxyurl + url).then((res) => {
      this.setState({ data: res.data });
    });
  }

  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    var d = this.state.data;
    if (!d) return <div className="loading"></div>;

    return (
      <div>
        <table className="table table-striped table-bordered">
          <thead>
            <tr>
              <th colSpan="2">
                <h6 className="mb-2">Downloads History</h6>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Today</td>
              <td>{d.today}</td>
            </tr>
            <tr>
              <td>Yesterday</td>
              <td>{d.yesterday}</td>
            </tr>
            <tr>
              <td>Last Week</td>
              <td>{d.last_week}</td>
            </tr>
            <tr>
              <th>All Time</th>
              <th>{d.all_time}</th>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}
export default DownloadsToday;
