import React, { Component, Fragment } from "react";
import axios from "./axios";

class ActiveVersion extends Component {
  state = {
    data: [],
  };

  componentDidMount() {
    var slug = localStorage.getItem("slug");
    const proxyurl = "https://cors-anywhere.herokuapp.com/";
    const url = "https://api.wordpress.org/stats/plugin/1.0/?slug=" + slug;
    axios.get(proxyurl + url).then((res) => {
      this.setState({ data: res.data });
    });
  }

  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const d = this.state.data;

    if (!d) return <div className="loading"></div>;

    return (
      <Fragment>
        <table className="table" style={{ fontSize: 17 }}>
          <tbody>
            {Object.entries(d).map((t, k) => (
              <tr key={k}>
                <td>{t[0]}</td>
                <td>{t[1]}%</td>
              </tr>
            ))}
          </tbody>
        </table>
      </Fragment>
    );
  }
}
export default ActiveVersion;
