import React from "react";
import { Route, withRouter } from "react-router-dom";

class AddSlug extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);

    this.state = {
      /*  Initial State */
      msg: "Add a plugin slug",
      className: "alert alert-info",
      slug: "",
    };
  }

  componentDidMount() {
    localStorage.setItem("slug", "3r-elementor-timeline-widget");
  }

  handleClick = (e) => {
    e.preventDefault();
    if (this.state.slug !== "") {
      localStorage.setItem("slug", this.state.slug);

      //window.location = "./plugin_stats";
      this.props.history.push("/plugin_stats");

      this.setState({
        msg: "Successfully Added.",
        className: "alert alert-success",
      });
    } else {
      this.setState({
        msg: "Please Insert a slug",
        className: "alert alert-danger",
      });
    }
  };
  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="col-md-6 mt-5 offset-3">
            <div className={this.state.className}>{this.state.msg}</div>

            <input
              type="text"
              className="form-control"
              name="slug"
              placeholder="contact-form-7"
              onChange={this.handleChange}
            />

            <button
              className="btn btn-primary btn-block mt-3"
              onClick={this.handleClick}
            >
              Add Slug{}
            </button>
            <Route
              exact
              path="/plugin_stats"
              render={(props) => (
                <button {...props} handleClick={this.handleClick} />
              )}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(AddSlug);
